.scaner {
    /*position: fixed;*/
    /*top: 12vh;*/
    /*left: 0;*/
    width: 100%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*background-color: var(--background);*/
    position:relative;
}

#qrCodeContainer {
    position: relative;
    width: 300px;
    height: 300px;
    border-radius: 2em;
    overflow: hidden;
    border: 1px solid var(--border);
    margin-bottom: 1em;


}

#qr-shaded-region {
    border-width: 50px !important;
}

video {
    position: absolute;
    top: 50%;
    left: 50%;
    width: auto !important;
    min-width: 100%;
    min-height: 100%;
    transform: translate(-50%, -50%);
}

.qr-message {
    /*position: absolute;*/
    /*top: 0;*/
    /*left: 0;*/
    /*width: 100%;*/
    /*height: 100%;*/
    padding: 0.4em;
    display: flex;
    justify-content: center;
    align-items: center;
    /*background-color: rgba(0, 0, 0, 0.5);*/
    font-size: 1em;
    color: var(--text-primary);
    text-align: center;
    word-break: break-all;

    border: 1px solid red;
}

.start-button {
    position: absolute;
    top: 1.5em;
    right: 1.5em;
    width: 40px;
    height: 40px;
    padding: 0;
    background-color: var(--star-primary);
    border: 0;
    border-radius: 7px;
    font-size: 20px;
    color: #fff;
}