.footer {
    /*position: fixed;*/
    position: sticky;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 10vh;
    background-color: var(--background);
    color: var(--text-secondary);
    display: flex;
    opacity: 80%;

    box-shadow: 0px -4px 4px -4px var(--text-secondary);

    z-index: 1;
}